import React from 'react';
// import {data} from './data.js';
import data from './data.js';

import './App.css';
import './assets/css/style.css';//src\assets\css\style.css



function GetUrlSite(prop) { 
    return (
        <a href={prop.url} className="text-color-heavenly" target="_blank" rel="noreferrer"> 
            <GetImg key = {prop.img} img = {prop.img}/>
        </a>
    )
}

function GetImg(prop) { 
    return <img src= {prop.img} alt="" className="card__image"/>
}

function GetTitle (props) { 
     return <h3 className="card__title">{props.title}</h3>
}

function GetUrlGithub (props) {
    return ( 
            <a className="card__url" href = {props.url} target="_blank" rel="noreferrer"> 
                <span style={{color: 'black'}}> GitHub: </span> {props.url}
            </a>
    )
}

function GetInfo (props) {
    return (
        <div>
            <p className="card__info">Сборка на {props.info} </p>
            <span className = "bottom">
                <GetUrelLogo url = {props.url}/>
            </span>
        </div>
     )
}

function GetUrelLogo (props) {
    return <img alt="Логотип программы " src={props.url} height="25" className = "bottom"/>
}


class Catalog extends React.Component {
    render() {
        let data = this.props.data;
        const rows = []; 
        let i = 0;
        data.forEach((data) => {
            rows.push(
                <article key = {data.id} className="card catalog__item" >
                    <div className="scale">
                        <GetUrlSite key = {data.urlSite + data.id} url = {data.urlSite} img = {data.img}/>
                    </div>
                    <div className="card__description">
                        <GetTitle key = {data.title}  title = {data.title}/>
                        <GetUrlGithub key = {data.urlGithub} url = {data.urlGithub}/>
                        <GetInfo key = {data.info + i}  info = {data.info} url = {data.urlLogo} /> 
                    </div>
                </article>
            );
            i++;
        });
        return <React.Fragment> {rows} </React.Fragment>
    }
}


export default class App extends React.Component {
    constructor(props) {
        super(props)
        this.data = data;
    }

    ListSiteGulp() {
        let data1 = [];
        for (const item of this.data) {
            if (item.info === 'GulpJS') {
                data1.push(item);
            } 
        }
        return data1;
    }
    
    ListSiteReact() {
        let data2 = [];
        for (const item of this.data) {
            if (item.info === 'React') {
                data2.push(item);
            } 
        }
        return data2;
    }

    ListSitePhpMyAdmin() {
        let data3 = [];
        for (const item of this.data) {
            if (item.info === 'PhpMyAdmin') {
                data3.push(item);
            } 
        }
        return data3;
    }

    ListSiteCodeIgniter() {
        let data4 = [];
        for (const item of this.data) {
            if (item.info === 'CodeIgniter') {
                data4.push(item);
            } 
        }
        return data4;
    }

    render() {
        const dataGulp = this.ListSiteGulp();
        const dataReact = this.ListSiteReact();
        const dataPhp = this.ListSitePhpMyAdmin();
        const dataCodeIgniter = this.ListSiteCodeIgniter();
        return (
            <>
                <Header/>
                <main className="catalog page__catalog">
                    <Catalog key = {dataGulp.toString()} data = {dataGulp}/>
                    <Catalog key={dataReact.toString()} data = {dataReact}/>
                    <Catalog key={dataPhp.toString()} data = {dataPhp}/>
                    <Catalog key={dataCodeIgniter.toString() + 'dataCodeIgniter' } data = {dataCodeIgniter}/>
                </main>
            </>
        )
    }
}


